	module.exports = {"data":{"content":{"content":"<h2 id=前言>前言</h2>\n<p>我們的網頁通常會有很多鏈接，但是在爬蟲的時候可以分開哪一些是外部鏈接，哪一些是內部鏈接來優化搜尋結果。比如說增加<code>nofollow</code>標籤。nofollow是一個HTML標籤的屬性值，它可以告訴搜尋引擎不要追蹤這個鏈接。</p>\n<h2 id=手動>手動</h2>\n<p>我們可以手動把每一個鏈接都加上nofollow屬性，比如這樣：</p>\n<pre><code><pre><code class=\"hljs javascript\">&lt;a href=\"https://github.com/calpa\" rel=\"nofollow\"&gt;Calpa的Github主頁&lt;/a&gt;\n&lt;a href=\"https://github.com/calpa\" rel=\"external nofollow\"&gt;Calpa的Github主頁&lt;/a&gt;\n</code></pre></code></pre>\n<p><code>external nofollow</code>是更加專業的寫法，告訴搜尋引擎這是一個外部鏈接。</p>\n<h2 id=自動>自動</h2>\n<p>我們也可以利用<code>hexo-autonofollow</code>插件來做這一件事情。\n只需要輸入<code>npm install hexo-autonofollow --save</code>，然後在<code>_config.yml</code>裡面寫上下面就可以了：</p>\n<pre><code><pre><code class=\"hljs javascript\">nofollow:\n    enable: <span class=\"hljs-literal\">true</span>\n    exclude:\n    - exclude1.com\n    - exclude2.com\n</code></pre></code></pre>\n<p>exclude1.com和exclude2都是內部域名。</p>\n<h2 id=參考資料>參考資料</h2>\n<ol>\n<li><a href=\"https://liuzhichao.com/2016/hexo-auto-nofollow.html\">Hexo优化之为外部链接添加nofollow\n</a></li>\n</ol>\n","title":"添加 nofollow 屬性優化 SEO","createdDate":"2017-02-25T22:00+08:00","headerImgur":null,"id":"2lrzxyQBL2MOMqwkO6weW6","toc":["前言","手動","自動","參考資料"],"tags":"Hexo, SEO"},"latestPosts":{"totalCount":60,"edges":[{"node":{"title":"【網易考拉校招】部門總監面對面-直播答疑邀約（附網易內推碼B222E54）","url":"netease-kaola-live","createdDate":"2018-08-01T20:00+08:00"}},{"node":{"title":"Uncaught ReferenceError: regeneratorRuntime is not defined 解決方法","url":"regenerator-runtime-is-not-defined","createdDate":"2018-07-29T20:00+08:00"}},{"node":{"title":"關於作者","url":"about-calpa-liu","createdDate":"2018-05-01T20:00+08:00"}},{"node":{"title":"【方法】在直接複製貼上內容的時候，可能已經不知不覺注入了一段不可視的「空白」","url":"insert-zero-width-characters","createdDate":"2018-04-06T23:00+08:00"}},{"node":{"title":"知乎無限 live 數據流網站","url":"zhihu-unlimited-lives-website","createdDate":"2018-03-28T20:49+08:00"}},{"node":{"title":"Gatsbyjs async/await 更新報錯解決方法","url":"gatsybjs-async-await-upgrade-bug-solution","createdDate":"2018-03-21T20:00+08:00"}}]}},"pathContext":{"id":"2lrzxyQBL2MOMqwkO6weW6"}}

